// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'//引入element-ui
import 'element-ui/lib/theme-chalk/index.css'//引入element-ui样式
import 'jquery'//引入jquery
import 'element-ui/lib/theme-chalk/display.css'//elemnt-ui的样式
import animate from 'animate.css'//引用animate动画库
//引入axios
import axios from "axios";//////////(1)
//import 'babel-polyfill'//ie9不支持Promise，因此需要在项目入口main.js中打个补丁,否则无法发出请求axios

//npm install axios的时候默认会安装qs
// qs相关的问题请搜索"nodejs qs"或者看这里https://www.npmjs.com/package/qs
import qs from 'qs';//

Vue.prototype.$https = axios;//////////(2)
Vue.use(ElementUI)//使用element-ui
Vue.config.productionTip = false


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



