  name: "Home",
  data() {
    return {
      imageUrl:"",//用户头像上传的路径
      apipath:"http://api.diasos.cc",
      checkCode:"点击获取",//验证码
      loginstatus:sessionStorage.loginstatus,
      infosheight: $(".cominfos").height(), // 这里是给到了一个默认值 （这个很重要）
      isheight: "", //关于我们div的高度
      listdata: Object,
      //提交数据状态
      issub:sessionStorage.isssub,
      //全局变量
      comanyinformation:
        "缔亚智软（DiasSoftware Inc.）致力于高新技术应用领域应用软件开发、企业网站开发、安卓/苹果App、微信小程序等企业信息化解决方案。长期专注于全行业信息化解决方案领域，凭借多年在应用软件开发以及信息安全服务的积累的经验，缔亚智软已经成为该领域内著名的应用系统解决方案提供商，期待与您合作......",
      activeName: "1",
      color: "rgba(255, 255, 255, 0.8)",
      data: [
        "http://www.diasos.cc/templets/Dias/images/1.jpg",
        "http://www.diasos.cc/templets/Dias/images/3.jpg",
        "http://www.diasos.cc/templets/Dias/images/2.jpg"
      ],
      formdata: {
        userid:sessionStorage.userid,
        apptype: "",
        clientname:sessionStorage.username,
        clientphone:sessionStorage.userphone,
        appdescriptions:""
      },
      infos: {
        title:"",
        description:"",
        img:"",
        content:""
      },
      htmltxt:"",
      htmltxtnews:"",
      isbool: true,
      logindata:{
        loginphone:"",
        loginpassword:"",
        logincode:""
      },
      registerdata:{
        regphone:"",
        regname:"",
        regpass:"",
        regpassword:"",
        regcode:""
      },
      userinfos:{
        $userid:sessionStorage.userid,
        $userphone:sessionStorage.userphone,
        $username:sessionStorage.username,
        $userimg:sessionStorage.userimg,
        $usernum:sessionStorage.usernum,
        $usernetname:sessionStorage.usernetname,
        $userage:sessionStorage.userage,
        $usersex:sessionStorage.usersex,
        $userbirth:sessionStorage.userbirth,
        $userprovince:sessionStorage.userprovince,
        $usercity:sessionStorage.usercity,
        $usercounty:sessionStorage.usercounty,
        $useraddress:sessionStorage.useraddress,
        $useridcard:sessionStorage.useridcard,
        $userothers:sessionStorage.userothers,
        $userregtime:sessionStorage.userregtime
      }
    };
  },
  computed: {
    isLogin() {
      return (
        !!this.formdata.apptype &&
        !!this.formdata.clientname &&
        !!this.formdata.clientphone &&
        !!this.formdata.appdescriptions
      );
    },
    isloging(){
      !!this.logindata.loginphone&&
      !!this.logindata.loginpassword&&
      !!this.logindata.logincode
    },
    isreg(){
      !!this.registerdata.regphone&&
      !!this.registerdata.regname&&
      !!this.registerdata.regpass&&
      !!this.registerdata.regpassword&&
      !!this.registerdata.regcode
    }
  },
  mounted() {
    this.loadingarticle();
    this.isheight = $(".cominfos").outerHeight(true);
    console.log(this.isheight);

    //监听窗口变化
    const that = this;
    window.onresize = () => {
      return (() => {
        that.infosheight = $(".cominfos").height();
        $(".cominfosss").css("height", that.infosheight);
      })();
    };
  },
  watch: {
    screenWidth(val) {
      if (!this.timer) {
        this.infosheight = val;
        this.timer = true;
        let that = this;
        setTimeout(function() {
          // that.screenWidth = that.$store.state.canvasWidth
          console.log(that.infosheight);
          that.init();
          that.timer = false;
        }, 400);
      }
    }
  },
  methods: {
    loadingarticle() {
      //详细信息,从服务器请求数据
      this.$https
        .get(this.apipath+"/index.php/API/InterFace/getarticle", {
          id: 1
        })
        .then(res => {
          console.log(res["data"]);
          this.listdata = res["data"];
        });
    },
    //事件
    viewcominfos() {
      //查看详细信息
      //详细信息,从服务器请求数据
      this.$https
        .get(this.apipath+"/index.php/API/InterFace/index", { id: 123 })
        .then(res => {
          console.log(res.data[0].content);
          this.htmltxt = res.data[0].content;
        });
      $(".dialogwin").addClass("animated zoomInDown diawinshow");

      //加载效果
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      setTimeout(() => {
        loading.close();
      }, 1500);
    },
    closewin() {
      $(".dialogwin").removeClass("animated zoomInDown diawinshow");
    },
    closenewswin() {
      $(".dialogwinnews").removeClass("animated zoomInRight diawinshow");
    },
    userlogin() {
      $(".loginwin").addClass("animated zoomInRight diawinshow");
      //加载效果
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      setTimeout(() => {
        loading.close();
      }, 1500);
    },
    userregister() {
      $(".registerwin").addClass("animated zoomInRight diawinshow");

      //加载效果
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      setTimeout(() => {
        loading.close();
      }, 1500);
    },
    toregister(){
      $(".loginwin").removeClass("animated zoomInRight diawinshow");
      $(".registerwin").addClass("animated zoomInRight diawinshow");

      //加载效果
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      setTimeout(() => {
        loading.close();
      }, 1500);
    },
    loginexit(){
      $(".loginwin").removeClass("animated zoomInRight diawinshow");
    },
    registerexit(){
      $(".registerwin").removeClass("animated zoomInRight diawinshow");
    },
    submitform() {
      if (this.issub) {
        this.$message({
          showClose: true,
          message: "您已经提交过数据，不需要重复提交数据！",
          type: "error"
        });
        return;
      }
      let url = this.apipath+"/index.php/API/InterFace/getbusinesses";
      this.$https({
        method: "post",
        url: url,
        params: {
          userid:this.formdata.userid,
          apptype: this.formdata.apptype,
          clientname: this.formdata.clientname,
          clientphone: this.formdata.clientphone,
          appdescriptions: this.formdata.appdescriptions

        }
      })
        .then(res => {
          this.$message({
            showClose: true,
            message: res["data"][0].msg,
            type: "success"
          });
          //清空输入的数据
          this.formdata.userid = "";
          this.formdata.apptype = "";
          this.formdata.appdescriptions = "";
          this.formdata.clientname = sessionStorage.username;
          this.formdata.clientphone = sessionStorage.userphone;
          sessionStorage.isssub = true;
          this.issub=sessionStorage.isssub;
        })
        .catch(err => {
          console.log(err);
          this.$message({
            showClose: true,
            message: "网络错误！请稍候重试！",
            type: "error"
          });
        });
    },
    subreg(){
        //检测验证码是否正确
        if(this.registerdata.regcode.toUpperCase()!=this.checkCode.toUpperCase()){
          this.$message({
                  showClose: true,
                  message: "您输入的验证码不正确",
                  type: "error"
          });
          return;
        }
      if (this.registerdata.regpass!==this.registerdata.regpassword) {
          this.$message({
            showClose: true,
            message: "两次输入密码不相同",
            type: "error"
          });
      }else{
        //console.log("1---------------------------");
        let apiurl = this.apipath+"/index.php/API/InterFace/userreg";
        this.$https({
          method: "post",
          url: apiurl,
          params: {
            userphone: this.registerdata.regphone,
            username: this.registerdata.regname,
            userpassword: this.registerdata.regpassword
          }
        }).then(res => {
          console.log(res);
            if (res["data"][0].status=="true") {
              this.$message({
                showClose: true,
                message: res["data"][0].msg,
                type: "error"
              });
              //注册成功跳转到登登录
              $(".registerwin").removeClass("animated zoomInRight diawinshow");
              $(".loginwin").addClass("animated zoomInRight diawinshow");
              //清空输入的数据
              this.registerdata.regphone = "";
              this.registerdata.regname = "";
              this.registerdata.regpass = "";
              this.registerdata.regpassword = "";
              this.registerdata.regcode="";
            }else{
              this.$message({
